import React from 'react';
import { useForm, ValidationError } from '@formspree/react';

function ContactForm() {
  const [state, handleSubmit] = useForm("mjvarboz");
  if (state.succeeded) {
      return <p>Sent!</p>;
  }
  return (
      <form id="contactForm" onSubmit={handleSubmit}>
        <div className="contactFormInputs">
            <label htmlFor="fullname">
            </label>
            <input 
                type="text"
                name="name"
                id="fullname"
                placeholder="Name"
            />
        </div>
        <div className="contactFormInputs">
            <label htmlFor="email">
            </label>
            <input
                type="email" 
                name="email"
                id="email"
                placeholder="Email"
            />
            <ValidationError 
                prefix="Email" 
                field="email"
                errors={state.errors}
            />
        </div>
        <div className="contactFormInputs">
            <label htmlFor="message">
            </label>
            <textarea
                id="message"
                name="message"
                placeholder="Message"
            />
            <ValidationError 
                prefix="Message" 
                field="message"
                errors={state.errors}
            />
        </div>
        <div className="contactFormButton">
            <button id="contactButton" type="submit" disabled={state.submitting}>
                SEND
            </button>
        </div>
    </form>
  );
}

export default ContactForm;