import { useState } from "react";
import './App.css';
import About from "./components/About";
import Contact from "./components/Contact";
import Projects from "./components/Projects";
import Footer from "./components/Footer";
import Typewriter from "typewriter-effect";

enum Page {
  Home = 1,
  About,
  Projects,
  Contact,
}
function App(): JSX.Element {
  const [page, setPage] = useState<Page>(Page.Home);

  function handlePageChange(pageTo: Page) {
    setPage(pageTo);
  }

  if (page === Page.About) {
    return (
      <div>
        <div className="nav">
          <div className="buttons">
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Home)}>
                HOME
              </button>
            </div>
            <div className="button">
              <button id="button-pageCurrent" onClick={() => handlePageChange(Page.About)}>
                ABOUT
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Projects)}>
                PROJECTS
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Contact)}>
                CONTACT
              </button>
            </div>
          </div>
          <div className="buttons">
            <div className="button">
              <button id="button-qali" onClick={() => handlePageChange(Page.Home)}>
                Q
              </button>
            </div>
          </div>
        </div>
        <div>
          <About />
        </div>
      </div>
    )
  }

  if (page === Page.Contact) {
    return (
      <div>
        <div className="nav">
          <div className="buttons">
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Home)}>
                HOME
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.About)}>
                ABOUT
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Projects)}>
                PROJECTS
              </button>
            </div>
            <div className="button">
              <button id="button-pageCurrent" onClick={() => handlePageChange(Page.Contact)}>
                CONTACT
              </button>
            </div>
          </div>
          <div className="buttons">
            <div className="button">
              <button id="button-qali" onClick={() => handlePageChange(Page.Home)}>
                Q
              </button>
            </div>
          </div>
        </div>
        <div>
          <Contact />
        </div>
      </div>
    )
  }

  if (page === Page.Projects) {
    return (
      <div>
        <div className="nav">
          <div className="buttons">
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Home)}>
                HOME
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.About)}>
                ABOUT
              </button>
            </div>
            <div className="button">
              <button id="button-pageCurrent" onClick={() => handlePageChange(Page.Projects)}>
                PROJECTS
              </button>
            </div>
            <div className="button">
              <button id="button-pageChange" onClick={() => handlePageChange(Page.Contact)}>
                CONTACT
              </button>
            </div>
          </div>
          <div className="buttons">
            <div className="button">
              <button id="button-qali" onClick={() => handlePageChange(Page.Home)}>
                Q
              </button>
            </div>
          </div>
        </div>
        <div>
          <Projects />
        </div>
      </div>
    )
  } else {
    return (
      <div className="main">
        <div className="buttons">
          <div className="button">
            <button id="button-pageCurrent" onClick={() => handlePageChange(Page.Home)}>
              HOME
            </button>
          </div>
          <div className="button">
            <button id="button-pageChange" onClick={() => handlePageChange(Page.About)}>
              ABOUT
            </button>
          </div>
          <div className="button">
            <button id="button-pageChange" onClick={() => handlePageChange(Page.Projects)}>
              PROJECTS
            </button>
          </div>
          <div className="button">
            <button id="button-pageChange" onClick={() => handlePageChange(Page.Contact)}>
              CONTACT
            </button>
          </div>
        </div>
        <div className="main-title">
          <h1>Qali</h1>
          <hr id="h1-line"></hr>
          <p>
          <Typewriter
            options={{
              strings: ['Welcome to my website!', 'Check out my projects.'],
              autoStart: true,
              loop: true,
            }}
          /></p>
        </div>
        <div className="footer-section">
            <Footer />
        </div>
      </div>
    );
  };
}

export default App;
